/* inter-regular - latin */
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../../public/assets/fonts/inter-v12-latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("../../public/assets/fonts/inter-v12-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../../public/assets/fonts/inter-v12-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../../public/assets/fonts/inter-v12-latin-regular.woff")
      format("woff"),
    /* Modern Browsers */
      url("../../public/assets/fonts/inter-v12-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../public/assets/fonts/inter-v12-latin-regular.svg#Inter")
      format("svg"); /* Legacy iOS */
}

